.App {
  text-align: center;
}
* {box-sizing: border-box;}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

pre {
  background: #383737;
  border: 1px solid #ddd;
  border-left: 3px solid #f36d33;
  color: white;
  page-break-inside: avoid;
  font-family: monospace;
  font-size: 15px;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1em 1.5em;
  display: block;
  border-radius: 20px;
  word-wrap: break-word;
}

img {
  width: 100%;
  height: auto;
}


@media screen and (max-width: 950px) {
  iframe {
    width: 100%;
  }
}
