.card-container {
  /* flex-direction: column; */
  background-color: #fff;
  border: 1px solid grey;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0px 0px 5px #c5c2c2;
  cursor: pointer;
  /* -moz-osx-font-smoothing: grayscale;
  backface-visibility: hidden; */
  /* transform: translateZ(0);
  transition: transform 0.25s ease-out; */
  word-break: break-all;
  /* display: inline-block; */
  margin: 0 0 1em;
  /* width: 100%; */
  /* height: fit-content; */
}

.image_home {
  width: 100%;
}

.card-container:hover {
  /* transform: scale(1.05); */
  border: 1px solid rgb(97, 97, 255);
}

.desc_card {
  color: #211922;
  font-size: 15px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  font-size: 13px;
  text-align: left;
  -webkit-box-orient: vertical;
}

.title_home{
  text-align: left;
  font-size: 30px;
  letter-spacing: -.04em;
  color: #353535;
  font-size: 18px;
  margin-bottom:0px
}

@media only screen and (max-width: 1100px) {
  .card-container {
    width:100%;
  }
}
