.topSpot {
    margin-top:25px;
}

.loadmore_comments {
    color: white;
    background-color: #3f51b5;
    border: none;
    padding: 4px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 12px;
    outline:none
}

.hg_of_comments {
    height: 400px;
    overflow: auto;
}

  ::-webkit-scrollbar {
    width: 7px;
}
 
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(133, 133, 133, 0.199); 
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}