    padding: 10px ;
    margin: 5px 10px 25px;
    border-radius: 5px;
    background-color: #eceaea6b;
    text-align: left;
}

.date_comment{
    /* width: 95%; */
    word-break: break-all;
    font-size: 11px;
    float: right;
    padding-left: 2px;
    color: #d8d8d8;
}

.author {
    /* float: left; */
    font-size: 13px;
    font-weight: 700;
    /* padding-right: 10px; */
}


.arrow_box {
	position: relative;
	background: #f5f5f5;
	/* border: 4px solid #c2e1f5; */
}
.arrow_box:after, .arrow_box:before {
	top: 100%;
	left: 10%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.arrow_box:after {
	border-color: rgba(136, 183, 213, 0);
	border-top-color: #f5f5f5;
	border-width: 12px;
	margin-left: -12px;
}
.arrow_box:before {
	border-color: rgba(194, 225, 245, 0);
	border-top-color: #f5f5f5;
	border-width:18px;
	margin-left: -18px;
}